.Footer{
  background-color: #2b2837;
  padding: 21px 160px 27px 160px;
  font-size: 14px;
  color: #7b7495;
}
.Bottom, .Top{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
}
.Bottom{
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.Top{
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-bottom: 20px;
  border-bottom: 1px solid #3c3b3b;
  margin-bottom: 21px;
}
.Logo{
  font-size: 24px;
  color: #fff;
  margin-right: 119px;
  display: inline-block;
}
.NavItem{
  padding-right: 50px;
}
.NavItem > a{
  color: inherit;
  text-decoration: none;
}
.NavItemHelp, .Word{
  text-transform: uppercase;
  font-weight: 700;
}
.Word{
  padding-right: 51px;
}
.SocialIconTelegram{
  padding-right: 44px;
}
.SocialIconFacebook{
  padding-right: 40px;
}
.TradingVolume > span:first-child{
  padding-left: 39px;
}
.TradingVolume > span:nth-child(2){
  padding-left: 30px;
}
.TradingVolume .Digits{
  color: #fff;
}



